<template>
  <div id="app">
    <div class="select-wrap">
      <div class="title">Canvas</div>
      <div class="rainbow-wrap">
        <div
          v-for="i in [{
            label:'width',
            value:'width'
          },{
            label:'height',
            value:'height'
          }]"
          :key="i.label"
          class="item"
        >
          <div class="label">{{ i.label }}</div>
          <el-input v-model="config[i.value]"></el-input>
        </div>
        <el-button type="primary" round @click="a" :disabled="waves.length > 5">Add waves</el-button>
        <el-button type="primary" round @click="config.direction='top'">top</el-button>
        <el-button type="primary" round @click="config.direction='left'">left</el-button>
        <el-button type="primary" round @click="config.direction='right'">right</el-button>
        <el-button type="primary" round @click="config.direction='bottom'">bottom</el-button>
      </div>

      <div class="title">Waves</div>
      <div class="waves-wrap">
        <div v-for="(w,k) in waves" :key="k" class="wave">
          <div v-for="(v,i,x) in w" :key="x" class="item">
            <div v-if="i !== 'background'" class="label">{{ obj[i] }}</div>
            <el-input v-if="i !== 'background'" :value="v" :class="i === 'color' && 'color'" @input="(o) =>c(k,i,o)"></el-input>
          </div>
          <el-button type="danger" icon="el-icon-delete" circle @click="d(k)" :disabled="waves.length === 1"></el-button>
        </div>
      </div>
    </div>
    <div class="show-wrap">
      <rainbow-waves :config="config" :waves="waves"/>
    </div>
  </div>
</template>

<script>
import RainbowWaves from "rainbow-waves";
export default {
  name: "App",
  components: {
    "rainbow-waves": RainbowWaves,
  },
  data() {
    return {
      obj: {
        // color: "wave-color",
        jitter: "jitter",
        restore: "restore",
        waveGap: "wave-gap",
        waterGap: "water-gap",
        waveUps: "wave-ups",
        bit: "wave-bit",
      },
      config: {
        el: "rainbow-waves",
        width: 100,
        height: 100,
        clear: true,
        direction:"bottom",
        background: {
          type: "gradient",
          color: ["#fff", "orange"],
          position: [0, 0, 1920, 0],
        },
      },
      waves: [
        {
          background: {
          type: "gradient",
          color: ["green", "#fff"],
          position: [0, 0, 1920, 0],
        },
          jitter: 0.04,
          restore: 0.03,
          waveGap: 80,
          waterGap: 20,
          waveUps: 6,
          bit: 0.3,
        },
      ],
    };
  },
  mounted(){
    this.config.width = window.innerWidth;
    this.config.height = window.innerHeight;
  },
  methods: {
    r() {
      let r = Math.floor(Math.random() * 256);
      let g = Math.floor(Math.random() * 256);
      let b = Math.floor(Math.random() * 256);
      let alpha = Math.random().toFixed(1);
      return `rgb(${r},${g},${b},${alpha})`;
    },
    d(k) {
      this.waves.splice(k, 1);
    },
    a() {
      let color = this.r();
      // let jitter = Number(
      //   (Math.random() * (0.09 - 0.01 + 0.04) + 0.09).toFixed(2)
      // );
      let jitter = 0.04;
      let restore = 0.03;
      let waveGap = Math.ceil(Math.random() * (100 - 40 + 1) + 40);
      let waterGap = Math.ceil(Math.random() * (40 - 20 + 1) + 20);
      let waveUps = Math.ceil(Math.random() * (10 - 5 + 1) + 5);
      let bit = 0.3;

      this.waves.push({
        background: {
          type: "color",
          color,
        },
        jitter,
        restore,
        waveGap,
        waterGap,
        waveUps,
        bit,
      });
    },
    c(key, item, value) {
      this.waves[key][item] = value;
    },
  },
};
</script>

